<template>
    <div :style="gradientStyle" style="height: 100vh;">
        <div style="display: flex;">
            <el-card style="width: 70%;margin-left: 270px;background-color: rgba(255, 255, 255, 0.5);margin-top: 10px;">
                <div style="font-size: 30px;font-weight: bold;margin: 0 0 10px 5px;">
                    <span>{{ pokemon.name }}</span>
                    <img :src="require('@/assets/images/normal/' + this.pokemon.id + '.png')" alt=""
                        style="height: 183px;float: right;margin-right: 40px;">
                </div>
                <div style="display: flex;">
                    <div v-if="colorMap[pokemon.type1]" class="attribute-box"
                        :style="{ backgroundColor: colorMap[pokemon.type1] }">
                        <a href="#" style="text-decoration: none;color: #fff;" >{{
                            pokemon.type1 }}</a>
                    </div>
                    <div v-if="pokemon.type2 && colorMap[pokemon.type2]" class="attribute-box"
                        :style="{ backgroundColor: colorMap[pokemon.type2] }">
                        <a href="#" style="text-decoration: none;color: #fff">{{
                            pokemon.type2 }}</a>
                    </div>
                    
                </div>
                <div style="display: flex;width: 180px;">
                    <div class="attribute-box" style="border-radius: 10%;border: 1px solid black;margin-top: 10px;">
                        <a href="#" style="text-decoration: none;color: black;" @click="characteristic(pokemon.ability1)">{{
                            pokemon.ability1 }}</a>
                    </div>
                    <div v-if="pokemon.ability2" class="attribute-box"
                        style="border-radius: 10%;margin-top: 10px;border: 1px solid black;">
                        <a href="#" style="text-decoration: none;color: black" @click="characteristic(pokemon.ability2)">{{
                            pokemon.ability2 }}</a>
                    </div>
                    <div v-if="pokemon.abilityHide" class="attribute-box"
                        style="border-radius: 10%;margin-top: 10px;border: 1px solid black;">
                        <a href="#" style="text-decoration: none;color: black" @click="characteristic(pokemon.abilityHide)">{{
                            pokemon.abilityHide }}</a>
                    </div>
                </div>
                <div v-if="pokemon.desc"
                        style="display: flex;border-radius: 10%;font-size: 20px;margin-top: 10px;border: 1px solid black;">
                        <a style="text-decoration: none;color: black">{{
                            pokemon.desc }}</a>
                </div>
            </el-card>
        </div>
        <el-card style="margin-top: 20px;width: 70%;margin-left: 270px;background-color: rgba(255, 255, 255, 0.5);">
            <div style="display: flex;align-items: center;">
                <div style="justify-content: center;font-size: 30px;">
                    等级：
                </div>
                <el-input style="width:70px;margin-right: 20px;" v-model="grade"></el-input>
                <div style="justify-content: center;font-size: 30px;">
                    性格：
                </div>
                <el-button style="width: 90px;height: 39.33px;" type="text"
                    @click="dialogTableVisible = true"><span style="font-size: 25px;">{{ this.nature.nature || '请选择性格' }}</span></el-button>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 125px;align-items: center;justify-content: center;font-size: 30px;margin-right: 80px;border-radius: 10%;">
                    种族值
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 80px;border-radius: 10%;">
                    个体值
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 80px;border-radius: 10%;">
                    努力值
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    能力值
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    HP:{{ pokemon.stat[0] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="validateInput('hp', $event)"
                        v-model="hp"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('hp', $event)"
                        v-model="evs.hp"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    <!-- {{ (pokemon.stat[0]×2＋this.hp＋努力值÷4)×等级÷100＋10＋等级 }} -->
                    {{ finalHP }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    AT:{{ pokemon.stat[1] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' style="width:70px;margin-right: 20px;" @input="validateInput('at', $event)"
                        v-model="at"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('at', $event)"
                        v-model="evs.at"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    <!-- （（攻击种族值×2＋攻击个体值＋攻击努力值÷4）×等级÷100＋5）×性格修正 -->
                    {{ finalStats.attack }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    DF:{{ pokemon.stat[2] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' style="width:70px;margin-right: 20px;" @input="validateInput('df', $event)"
                        v-model="df"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('df', $event)"
                        v-model="evs.df"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    <!-- （（防御种族值×2＋防御个体值＋防御努力值÷4）×等级÷100＋5）×性格修正 -->
                    {{ finalStats.defense }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    SA:{{ pokemon.stat[3] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' style="width:70px;margin-right: 20px;" @input="validateInput('sa', $event)"
                        v-model="sa"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('sa', $event)"
                        v-model="evs.sa"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    {{ finalStats.specialAttack }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    SD:{{ pokemon.stat[4] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' style="width:70px;margin-right: 20px;" @input="validateInput('sd', $event)"
                        v-model="sd"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('sd', $event)"
                        v-model="evs.sd"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    {{ finalStats.specialDefense }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    SP:{{ pokemon.stat[5] }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' style="width:70px;margin-right: 20px;" @input="validateInput('sp', $event)"
                        v-model="sp"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" @input="hpSuygInput('sp', $event)"
                        v-model="evs.sp"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    {{ finalStats.speed }}
                </div>
            </div>
            <div style="margin-top: 20px;display: flex;">
                <div
                    style="width: 100px;justify-content: center;font-size: 30px;margin-right: 113px;border-radius: 10%;">
                    TT:{{ totalStats }}
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 85px;border-radius: 10%;">
                    <el-input :max='31' disabled style="width:70px;margin-right: 20px;"></el-input>
                </div>
                <div style="justify-content: center;font-size: 30px;margin-right: 70px;border-radius: 10%;">
                    <el-input style="width:70px;margin-right: 20px;" disabled :value="totalEVs"></el-input>
                </div>
                <div style="width: 100px;justify-content: center;font-size: 30px;border-radius: 10%;">
                    能力值
                </div>
            </div>
        </el-card>
        <div>

        </div>

        <el-dialog title="请选择性格" :visible.sync="dialogTableVisible">
            <el-table :data="gridData" border height="400" highlight-current-row @row-click="handleRowClick">
                <el-table-column property="nature" label="性格" width="150"></el-table-column>
                <el-table-column property="jia" label="+" width="200"></el-table-column>
                <el-table-column property="jian" label="-"></el-table-column>
            </el-table>
        </el-dialog>
    </div>

</template>


<script>

export default {
    name: 'Information',
    data() {
        return {
            dialogTableVisible: false,
            nature: { nature: '勤奋', jia: '--', jian: '--' }, // 性格
            pokemon: JSON.parse(localStorage.getItem("information") || '{}'), // 获取数据
            grade: 50, // 等级
            hp: 31, // hp
            at: 31,
            df: 31,
            sa: 31,
            sd: 31,
            sp: 31,
            Sum: 0,
            evs: {
                hp: 0,
                at: 0,
                df: 0,
                sa: 0,
                sd: 0,
                sp: 0
            },
            colorMap: {
                '草': '#77CC55',
                '火': '#FF4422',
                '毒': '#AA5599',
                '飞行': '#3366cc',
                '水': '#3399FF',
                '虫': '#AABB22',
                '一般': '#BBBBAA',
                '恶': '#775544',
                '电': '#FFCC33',
                '超能力': '#FF5599',
                '地面': '#DDBB55',
                '冰': '#77DDFF',
                '钢': '#AAAABB',
                '妖精': '#FFAAFF',
                "龙": '#7766EE',
                '格斗': '#BB5544',
                '幽灵': '#704170',
                '岩石': '#afa981'
            },
            gridData: [
                { nature: '勤奋', jia: '--', jian: '--' },
                { nature: '怕寂寞', jia: 'at', jian: 'df' },
                { nature: '固执', jia: 'at', jian: 'sa' },
                { nature: '顽皮', jia: 'at', jian: 'sd' },
                { nature: '勇敢', jia: 'at', jian: 'sp' },
                { nature: '坦率', jia: '--', jian: '--' },
                { nature: '大胆', jia: 'df', jian: 'at' },
                { nature: '淘气', jia: 'df', jian: 'sa' },
                { nature: '乐天', jia: 'df', jian: 'sd' },
                { nature: '悠闲', jia: 'df', jian: 'sp' },
                { nature: '害羞', jia: '--', jian: '--' },
                { nature: '内敛', jia: 'sa', jian: 'at' },
                { nature: '慢吞吞', jia: 'sa', jian: 'df' },
                { nature: '马虎', jia: 'sa', jian: 'sd' },
                { nature: '冷静', jia: 'sa', jian: 'sp' },
                { nature: '浮躁', jia: '--', jian: '--' },
                { nature: '温和', jia: 'sd', jian: 'at' },
                { nature: '温顺', jia: 'sd', jian: 'df' },
                { nature: '慎重', jia: 'sd', jian: 'sa' },
                { nature: '自大', jia: 'sd', jian: 'sp' },
                { nature: '认真', jia: '--', jian: '--' },
                { nature: '胆小', jia: 'sp', jian: 'at' },
                { nature: '急躁', jia: 'sp', jian: 'df' },
                { nature: '爽朗', jia: 'sp', jian: 'sa' },
                { nature: '天真', jia: 'sp', jian: 'sd' },
            ]

        }
    },
    created() {
        // 将字符串分割成数组，并将结果重新赋值给 pokemon.stat
        if (this.pokemon.stat && typeof this.pokemon.stat === 'string') {
            this.pokemon.stat = this.pokemon.stat.split(',').map(Number); // 转换成数字的数组
        }

    },
    computed: {
        gradientStyle() {
            const defaultColor = '#FFFFFF'; // 默认颜色
            const startColor = this.colorMap[this.pokemon.type1] || defaultColor;
            const endColor = this.colorMap[this.pokemon.type2] || defaultColor;
            return `background: linear-gradient(to right, ${startColor}, ${endColor})`;
        },
        totalStats() {
            return this.pokemon.stat.reduce((sum, value) => sum + value, 0);
        },
        totalEVs() {
            return Object.values(this.evs).reduce((total, currentValue) => Number(total) + Number(currentValue), 0);
        },
        natureEffects() {
            return this.getNatureEffect();
        },
        finalHP() {
            return this.calculateStat(this.pokemon.stat[0], this.hp, this.evs.hp, this.grade, 'hp', this.natureEffects);
        },
        // 通用能力值计算
        finalStats() {
            // 注意，这是一个计算属性，返回的是一个带有所有其他属性值的对象
            return {
                attack: this.calculateStat(this.pokemon.stat[1], this.at, this.evs.at, this.grade, 'at', this.natureEffects),
                defense: this.calculateStat(this.pokemon.stat[2], this.df, this.evs.df, this.grade, 'df', this.natureEffects),
                specialAttack: this.calculateStat(this.pokemon.stat[3], this.sa, this.evs.sa, this.grade, 'sa', this.natureEffects),
                specialDefense: this.calculateStat(this.pokemon.stat[4], this.sd, this.evs.sd, this.grade, 'sd', this.natureEffects),
                speed: this.calculateStat(this.pokemon.stat[5], this.sp, this.evs.sp, this.grade, 'sp', this.natureEffects),
            };
        },
    },
    methods: {
        validateInput(stat, value) {
            const parsedValue = parseInt(value, 10);
            if (!isNaN(parsedValue)) {
                this[stat] = parsedValue > 31 ? 31 : parsedValue;
            }
        },
        hpSuygInput(stat, value) {
            console.log(stat);
            if (value !== '') {
                const parsedValue = parseInt(value, 10); // 将输入值转换为整数
                if (!isNaN(parsedValue)) {
                    // 如果parsedValue是一个数字...
                    if (parsedValue > 252) {
                        // 努力值个别属性不能超过252
                        this.evs[stat] = 252;
                        this.sunEvs += 252;
                    } else if (this.totalEVs - this.evs[stat] + parsedValue > 510) {
                        // 总努力值不能超过510
                        console.log(this.evs[stat]);
                        console.log(this.totalEVs)
                        this.evs[stat] = 510 - (this.totalEVs - this.evs[stat]);
                    } else {
                        // 更新努力值
                        this.evs[stat] = parsedValue;
                    }
                } else {
                    // 如果不是数字，保持先前的值
                    // 可能的话，这里可以显示一个错误消息或反馈
                    this.$message.error("请输入数字")
                    this.evs[stat] = 0;
                }
            } else {
                // 如果输入为空，重置为0
                this.evs[stat] = 0;
            }
        },
        getNatureEffect() {
            // 此处需要确保我们通过`nature.nature`属性来访问选中的性格
            const natureInfo = this.gridData.find(natureItem => natureItem.nature === this.nature.nature);
            if (!natureInfo) return { increase: null, decrease: null };
            console.log(natureInfo);
            return {
                increase: natureInfo.jia !== '--' ? natureInfo.jia : null,
                decrease: natureInfo.jian !== '--' ? natureInfo.jian : null
            };
        },
        calculateStat(baseStat, iv, ev, level, stat, natureEffects) {
            const { increase, decrease } = natureEffects;
            let natureMultiplier = 1;

            if (stat === increase) {
                natureMultiplier = 1.1;
            } else if (stat === decrease) {
                natureMultiplier = 0.9;
            }
            console.log(natureEffects);
            // 判断是否计算HP
            if (stat === 'hp') {
                return Math.floor(((baseStat * 2 + iv + Math.floor(ev / 4)) * level / 100) + level + 10);
            } else {
                return Math.floor(Math.floor(((baseStat * 2 + iv + Math.floor(ev / 4)) * level / 100) + 5) * natureMultiplier);
            }
        },
        handleRowClick(row, column, event) {
            this.$set(this, 'nature', row);
            this.dialogTableVisible = false
        },
        characteristic(ability){
            localStorage.setItem("ability", JSON.stringify(ability))
            this.$router.push("/elfCharacteristic")
        }
    }
}
</script>
<style scoped>
.attribute-box {
    width: 70px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
    margin-left: 5px;
    border-radius: 10%;
    color: #FFF;
}

.attribute-ability {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
}
</style>